import uniSearchBar from '../../components/uni-ui/uni-search-bar/uni-search-bar.vue'
export default {

components: {
uniSearchBar
},
props: {},
data () {
  return {
	  page:1,
	  pageSize:12,
	  keywords:'',
	  rows:[]
    }
  },
  methods: {
    gethot(){
    	uni.request({
    		url: `${this.$api}/search/list?keywords=${this.keywords}&qq=122212489&page=${this.page}&pageSize=${this.pageSize}`,
    		method: 'POST',
    		data: {},
    		success: res => {
    			console.log(res);
                this.rows=res.data.data.rows
    		},
    		fail: () => {},
    		complete: () => {}
    	});
    },
	search(e){
		this.keywords=e.value
		this.gethot()
	},
	cancel(e){
		this.keywords=''
		this.gethot()
	},
	refresh(){
		this.pageSize+=12
		this.gethot()
	}
  },
  mounted () {
    this.gethot()
  },
  onLoad () {

  },
  filters: {

  },
  computed: {

  },
  watch: {

  },
  directives: {

  }
}
